video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.main-container {
  /* background-image: url("../../assets/keyboard.jpg"); */
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.main-container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: -100px;
}

.main-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.main-btn {
  margin-top: 32px;
}

.main-btn .btn {
  margin: 6px;
}

.text-container {
  width: 50%;
  text-align: center;
}

@media screen and (max-width: 960px) {
  .main-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .main-container > h1 {
    font-size: 50px;
    margin-top: -150px;
  }
  .main-container > p {
    font-size: 30px;
  }
  .btn-mobile {
    display: block;
    justify-content: center;

    text-decoration: none;
  }
  .btn {
    width: 100%;
  }
}
